.containerForecast {
  position: absolute;
  top: 1%;
  left: 70%;
  min-width: 19vw;
  margin: 5.5% auto;
  min-height: 65%;
  border: rgb(38, 57, 77);
  padding: 30px;
  border-radius: 5px;
  text-align: center;
  box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
  background-color: transparent;
  overflow: hidden;
}

@media screen and (max-width: 1200px) {
  .containerForecast {
    top: 140vh;
    left: 25vw;
    width: 40vw;
    height: 80vh;
  }
}

@media screen and (max-width: 700px) {
  .containerForecast {
    top: 100vh;
    left: 8vw;
    width: 70vw;
  }
}

.accordion {
  width: 100%;
  height: 65vh;
  background-color: transparent;
  margin: 0 auto;
  border-radius: 5px;
  border: 1px solid black;
  overflow-y: scroll;
}

@media screen and (max-width: 992px) {
  .accordion {
    height: 70vh;
  }
}

.accordion::-webkit-scrollbar {
  width: 15px;
}

.accordion::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgb(233, 232, 232);
  border-radius: 5px;
}

.accordion::-webkit-scrollbar-thumb {
  background: transparent;
  border-radius: 5px;
}

.accordion::-webkit-scrollbar-thumb:hover {
  background: transparent;
  box-shadow: inset 0 0 5px rgb(233, 232, 232);
}

.titleIcons {
  display: flex;
  justify-content: space-between;
  margin: 0 15px;
  margin-top: 2.3em;
  cursor: pointer;
  font-size: 12px;
  height: 100%;
}

@media screen and (max-width: 1200px) {
  .titleIcons {
    margin-top: 1.6em;
  }
}

.content {
  padding: 1.45%;
  background-color: rgb(71, 140, 180);
  font-size: 10px;
  word-spacing: 10px;
  letter-spacing: 0.3px;
}


    
    
    
    
    
