* {
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
}

body {
  background-image: url("./Assets/clouds.jpg");
  background-size: cover;
  background-attachment: fixed;
  overflow: hidden;
  box-sizing: border-box;
  width: 100vw;
}

@media screen and (max-width: 1200px) {
  body {
    overflow: visible;
    height: 300vh;
  }
}

.weatheroverview {
  max-width: 100vw;
  margin: 0 auto;
  overflow: hidden;
}

.containerCard {
  position: absolute;
  top: 1%;
  left: 38%;
  min-width: 19vw;
  margin: 5.5% auto;
  overflow: auto;
  min-height: 65%;
  border: rgb(38, 57, 77);
  padding: 30px;
  margin-bottom: 3vw;
  border-radius: 5px;
  text-align: center;
  box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
  background-color: transparent;
  overflow: hidden;
}

@media screen and (max-width: 1200px) {
  .containerCard {
    top: 15vw;
    width: 40vw;
    left: 25vw;
  }
}

@media screen and (max-width: 700px) {
  .containerCard {
    top: 15vw;
    width: 70vw;
    left: 8vw;
  }
}

@media screen and (max-width: 460px) {
  .containerCard {
    top: 22vw;
  }
}

.weatherOnly {
  border-radius: 10px;
  margin: 0 auto;
  background-color: rgba(233, 232, 232, 0.1);
  padding: 5% 0;
  min-width: 85%;
  margin-top: 10%;
}

.button {
  position: absolute;
  top: 63%;
  left: 47%;
  width: 5em;
  height: 5em;
  border-radius: 50%;
  color: black;
  border: solid black 3.5px;
  font-size: 90%;
  background-color: transparent;
  cursor: pointer;
  text-align: center;
  margin: 5% auto;
}

@media screen and (max-width: 1200px) {
  .button {
    left: 70vw;
    top: -2vw;
    box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
    border: rgb(38, 57, 77);
    width: 12vw;
    height: 12vw;
    font-size: 2vw;
  }
}

@media screen and (max-width: 460px) {
  .button {
    width: 20vw;
    height: 20vw;
    font-size: 3vw;
    left: 65vw;
  }
}
.buttonContainer {
  position: relative;
  max-width: 3vw;
  top: 4vw;
  left: -30vw;
  margin: 0 auto;
  overflow: visible;
  min-height: 70vh;
  padding: 2%;
  border-radius: 5px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

@media screen and (max-width: 1200px) {
  .buttonContainer {
    flex-direction: row;
    top: -0.5vw;
    left: -16vw;
    min-height: 5vw;
    max-width: 50vw;
    justify-content: space-around;
    align-items: center;
  }
}

.citybutton {
  width: 10vw;
  height: 10vw;
  border-radius: 50%;
  color: black;
  box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
  border: rgb(38, 57, 77);
  font-size: 1.2vw;
  background-color: transparent;
  cursor: pointer;
  text-align: center;
}

@media screen and (max-width: 1200px) {
  .citybutton {
    width: 12vw;
    height: 12vw;
    font-size: 2vw;
  }
}

@media screen and (max-width: 460px) {
  .citybutton {
    width: 20vw;
    height: 20vw;
    font-size: 3vw;
  }
}
